<template>
  <div class="container">
    <h2>Computers</h2>

    <app-alert
      :message="message"
    ></app-alert>

    <computer-form>

    </computer-form>

    <app-computers-list
      :computers="getComputers"
    ></app-computers-list>

  </div>
</template>

<script>
import AppComputersList from '@/components/AppComputersList'
import ComputerForm from '@/components/ComputerForm'
import AppAlert from '@/components/AppAlert'
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      message: ''
    }
  },
  methods: {
    async loadComputers () {
      await this.$store.dispatch('loadComputers', 'http://10.70.62.240:8080/computers')
    }
  },
  computed: {
    ...mapGetters(['getComputers'])
  },
  async mounted () {
    await this.loadComputers()
  },
  components: { AppComputersList, ComputerForm, AppAlert }
}
</script>

<style scoped lang="stylus">
.form-grid
  display flex
  flex-wrap wrap
.field
  min-width 25%

</style>
