<template>
  <div v-if="cabinets.length !== 0">
     <div class="card mt-2 p-3" v-for="cabinet in cabinets" :key="cabinet.number">
       <div>
          <div>Number: <router-link :to="`/cabinet/${cabinet.number}`">{{cabinet.number}}</router-link></div>
          <div>Floor: {{cabinet.floor}}</div>
          <div>Department: {{cabinet.department}}</div>
         <button
           @click="$emit('editCabinet', cabinet.number)"
           v-if="!isEdit"
         >Edit</button>
         <button @dblclick="$emit('deleteCabinet', cabinet.number)">Delete</button>
       </div>
    </div>
  </div>

  <div v-else>List is empty</div>
</template>

<script>
export default {
  emits: ['editCabinet'],
  props: ['cabinets', 'isEdit']
}
</script>

<style scoped lang="stylus">
  .buttons
    display: flex;
  .card
    display: flex;
    align-items: center
    flex-direction row
    justify-content: space-between;
</style>
