<template>
    <tr>
      <td>{{computer.name}}</td>
      <td>{{computer.ram}}</td>
      <td>{{computer.processor}}</td>
      <td>{{computer.ipAddress}}</td>
      <td>{{computer.cabinet}}</td>
      <td>{{computer.login}}</td>
      <td>{{computer.password}}</td>
      <td>
        <button
          @click="editComputer"
          class="btn btn-outline-dark"
        >Edit</button>
        <button
          @dblclick="deleteComputer"
          class="btn btn-outline-danger"
        >Delete</button>
      </td>
    </tr>

</template>

<script>
export default {
  emits: ['deleteComputer'],
  props: ['computer'],
  methods: {
    async deleteComputer () {
      await this.$store.dispatch('deleteComputer', {
        url: 'http://10.70.62.240:8080/computers',
        id: this.computer.name
      })
    },
    editComputer () {
      this.$store.dispatch('toggleEdit', { value: true })
      this.$store.dispatch('toggleCurrentComputer', { value: this.computer.name })
      this.$store.dispatch('editComputer')
    }
  }
}
</script>

