<template>
  <div class="container">

    <h2>Printers</h2>

    <form class="d-flex">
      <input type="text" class="form-control" placeholder="name" v-model="printer.name">
      <input type="text" class="form-control" placeholder="cartridge" v-model="printer.cartridge">
      <input type="text" class="form-control" placeholder="cabinet" v-model="printer.cabinet">
      <select
        class="form-select" v-model="printer.connectionType"
      >
        <option value="USB">USB</option>
        <option value="WIFI">WIFI</option>
        <option value="LAN">LAN</option>
      </select>
      <input type="text" class="form-control" placeholder="ipAddress" v-model="printer.ipAddress">
      <button
        class="btn btn-success"
        @click.prevent="addPrinter"
      >AddPrinter
      </button>
      <button
        class="btn btn-success"
        @click.prevent="savePrinter"
      >Save
      </button>
      <button
        class="btn btn-secondary"
        @click.prevent="clearForm"
      >Clear Form
      </button>
    </form>
    <table>
      <tr>
        <th>Name</th>
        <th>Cartridge</th>
        <th>Cabinet</th>
        <th>Connection Type</th>
        <th>IP Address</th>
      </tr>
      <tr v-for="printer in printers" :key="printer.id">
        <td>{{printer.name}}</td>
        <td>{{printer.cartridge}}</td>
        <td>{{printer.cabinet}}</td>
        <td>{{printer.connectionType}}</td>
        <td>{{printer.ipAddress}}</td>
        <td>
          <div class="d-flex">
            <button
              class="btn btn-warning"
              @click="editPrinter(printer.id)"
            >Edit
            </button>
            <button
              class="btn btn-danger"
              @dblclick="deletePrinter(printer.id)"
            >Delete
            </button>
          </div>

        </td>
      </tr>
    </table>

  </div>
</template>

<script>
import Rest, { authHeaders } from '@/rest'

export default {
  data () {
    return {
      printer: {
        name: '',
        cartridge: '',
        cabinet: '',
        connectionType: 'USB',
        ipAddress: ''
      },
      printers: [],
      currentPrinter: ''
    }
  },
  mounted () {
    this.loadPrinters()
  },
  methods: {
    async loadPrinters () {
      const { data } = await Rest.get('/printers', authHeaders())
      this.printers = [...data]
    },
    async addPrinter () {
      try {
        await Rest.post('/printers', {
          name: this.printer.name,
          cartridge: this.printer.cartridge,
          cabinet: this.printer.cabinet,
          connectionType: this.printer.connectionType,
          ipAddress: this.printer.ipAddress
        }, authHeaders()
        )
        this.clearForm()

        await this.loadPrinters()
      } catch (e) {
        console.error(e.message)
      }
    },
    async savePrinter (id) {
      try {
        await Rest.put(`/printers/${this.currentPrinter}`, {
          name: this.printer.name,
          cartridge: this.printer.cartridge,
          cabinet: this.printer.cabinet,
          connectionType: this.printer.connectionType,
          ipAddress: this.printer.ipAddress
        }, authHeaders()
        )
        this.clearForm()
        await this.loadPrinters()
      } catch (e) {
        console.error(e.message)
      }
    },
    async deletePrinter (id) {
      try {
        await Rest.delete(`/printers/del?id=${id}`, authHeaders())
        this.clearForm()
        await this.loadPrinters()
      } catch (e) {
        console.error(e.message)
      }
    },
    async editPrinter (id) {
      try {
        this.currentPrinter = id
        const { data } = await Rest.get(`/printers/${id}`, authHeaders())
        this.printer.name = data.name
        this.printer.cartridge = data.cartridge
        this.printer.cabinet = data.cabinet
        this.printer.connectionType = data.connectionType
        this.printer.ipAddress = data.ipAddress
      } catch (e) {
        console.error(e.message)
      }
    },
    clearForm () {
      this.currentPrinter = ''
      this.printer.name = ''
      this.printer.cartridge = ''
      this.printer.cabinet = ''
      this.printer.connectionType = ''
      this.printer.ipAddress = ''
    }
  }
}
</script>

<style scoped lang="stylus">
  table
    width 100%
</style>
