<template>
  <div class="container">
    <h2>Cabinets</h2>
    <form action="">
      <label>
        number
        <input type="text" v-model="cabinet.number">
      </label>
      <br>
      <label>
        floor
        <input type="text" v-model="cabinet.floor">
      </label>
      <br>
      <label>
        department
        <input type="text" v-model="cabinet.department">
      </label>
      <button
        @click.prevent="addCabinet"
        v-if="!isEdit"
      >Add</button>
      <button
        @click.prevent="saveCabinet"
        v-if="isEdit"
      >Save</button>
      <button
        @click.prevent="clearForm"
      >Cancel/Clear</button>
    </form>

    <app-cabinets-list
      :cabinets="cabinets"
      :is-edit="isEdit"
      @deleteCabinet="deleteCabinet"
      @editCabinet="editCabinet"
    ></app-cabinets-list>

  </div>
</template>

<script>
import axios from 'axios'
import AppCabinetsList from '@/components/AppCabinetsList'

export default {
  data () {
    return {
      cabinet: {
        number: '',
        floor: '',
        department: ''
      },
      currentCabinet: null,
      cabinets: [],
      isEdit: false
    }
  },
  methods: {
    async loadCabinets () {
      try {
        const { data } = await axios.get('http://10.70.62.240:8080/cabinets')
        this.cabinets = Object.keys(data).map(key => {
          return {
            ...data[key]
          }
        })
      } catch (e) {
        console.error(e.message)
      }
    },
    async addCabinet () {
      try {
        await axios.post('http://10.70.62.240:8080/cabinets', this.cabinet)
        await this.loadCabinets()
        this.clearForm()
      } catch (e) {
        console.error(e.message)
      }
    },
    async deleteCabinet (id) {
      try {
        await axios.delete(`http://10.70.62.240:8080/cabinets/del?number=${id}`)
        await this.loadCabinets()
      } catch (e) {
        console.error(e.message)
      }
    },
    async editCabinet (id) {
      try {
        this.currentCabinet = id
        this.isEdit = true
        const { data } = await axios.get(`http://10.70.62.240:8080/cabinets/${id}`)
        this.cabinet = {
          number: data.number,
          floor: data.floor,
          department: data.department
        }
        await this.loadCabinets()
      } catch (e) {
        console.error(e.message)
      }
    },
    async saveCabinet () {
      try {
        await axios.put(`http://10.70.62.240:8080/cabinets/${this.currentCabinet}`, this.cabinet)
        await this.loadCabinets()
        this.clearForm()
        this.isEdit = false
      } catch (e) {
        console.error(e.message)
      }
    },
    clearForm () {
      this.cabinet = {
        number: '',
        floor: '',
        department: ''
      }
      this.isEdit = false
      this.currentCabinet = null
    }
  },
  components: { AppCabinetsList },
  async mounted () {
    await this.loadCabinets()
  }
}
</script>

<style scoped lang="stylus">
.form-grid
  display flex
  flex-wrap wrap
.field
  min-width 25%

</style>
