<template>
    <h3>Computers in {{$route.params.id}} cab. Quantity: {{getComputers.length}}</h3>

    <computer-form>

    </computer-form>

    <app-computers-list
      :computers="getComputers"
    ></app-computers-list>
</template>

<script>
import AppComputersList from '@/components/AppComputersList'
import ComputerForm from '@/components/ComputerForm'
import { mapGetters } from 'vuex'
export default {
  methods: {
    async loadComputers () {
      await this.$store.dispatch('loadComputers', `http://10.70.62.240:8080/computers/cab?cabinet=${this.$route.params.id}`)
    }
  },
  computed: {
    ...mapGetters(['getComputers'])
  },
  async mounted () {
    await this.loadComputers()
  },
  components: { AppComputersList, ComputerForm }
}
</script>

