import axios from 'axios'

const instance = axios.create({
  baseURL: 'http://10.70.62.240:8080'
})

const USER_TOKEN = 'USER_TOKEN'
const USER_SESSION = 'USER_SESSION'
const USER_ADDRESS = 'USER_ADDRESS'

let session = localStorage.getItem(USER_SESSION)
let token = localStorage.getItem(USER_TOKEN)
let address = localStorage.getItem(USER_ADDRESS)

export function authHeaders () {
  if (!haveSession()) return { headers: {} }
  return {
    headers: {
      token: token,
      sessionId: session,
      ipAddress: address
    }
  }
}

export function haveSession () {
  return !!(session && token && address)
}

export function saveAuth (newSession, newToken, newAddress) {
  session = newSession
  token = newToken
  address = newAddress
  localStorage.setItem(USER_SESSION, session)
  localStorage.setItem(USER_TOKEN, token)
  localStorage.setItem(USER_ADDRESS, address)
}

export function clearAuth () {
  session = ''
  token = ''
  address = ''
  localStorage.removeItem(USER_TOKEN)
  localStorage.removeItem(USER_SESSION)
  localStorage.removeItem(USER_ADDRESS)
}

export default instance
