<template>
  <div class="container">
    <h2>Users</h2>
    <form class="d-flex">
      <input type="text" class="form-control" placeholder="Name" v-model="user.name">
      <input type="text" class="form-control" placeholder="Position" v-model="user.position">
      <input type="text" class="form-control" placeholder="Cabinet" v-model="user.cabinet">
      <input type="text" class="form-control" placeholder="Department" v-model="user.department">
      <input type="text" class="form-control" placeholder="PC Name" v-model="user.pcName">
      <button
        class="btn btn-success"
        @click.prevent="addUser"
      >AddUser
      </button>
      <button
        class="btn btn-success"
        @click.prevent="saveUser"
      >Save
      </button>
      <button
        class="btn btn-secondary"
        @click.prevent="clearForm"
      >Clear Form
      </button>
    </form>
    <table>
      <tr>
        <th>ID</th>
        <th>Name</th>
        <th>Position</th>
        <th>Cabinet</th>
        <th>Department</th>
        <th>PC Name</th>
      </tr>
      <tr v-for="user in users" :key="user.id">
        <td>{{user.id}}</td>
        <td>{{user.name}}</td>
        <td>{{user.position}}</td>
        <td>{{user.cabinet}}</td>
        <td>{{user.department}}</td>
        <td>{{user.pcName}}</td>
        <td>
          <div class="d-flex">
            <button
              class="btn btn-warning"
              @click="editUser(user.id)"
            >Edit
            </button>
            <button
              class="btn btn-danger"
              @dblclick="deleteUser(user.id)"
            >Delete
            </button>
          </div>

        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import Rest, { authHeaders } from '../rest'

export default {
  data () {
    return {
      user: {
        name: '',
        position: '',
        cabinet: '',
        department: '',
        pcName: ''
      },
      users: [],
      currentUser: ''
    }
  },
  methods: {
    async loadUsers () {
      // const { data } = await axios.get('http://10.70.62.240:8080/users')
      // console.log(await Rest.get('/users', authHeaders()))
      const { data } = await Rest.get('/users', authHeaders())
      this.users = [...data]
    },
    async addUser () {
      try {
        await Rest.post('/users', {
          name: this.user.name ?? '',
          position: this.user.position ?? '',
          cabinet: this.user.cabinet ?? '',
          department: this.user.department ?? '',
          pcName: this.user.pcName ?? ''
        }, authHeaders())
        this.clearForm()
        await this.loadUsers()
      } catch (e) {
        console.error(e.message)
      }
    },
    async deleteUser (id) {
      await Rest.delete(`/users/${id}`, authHeaders())
      this.clearForm()
      await this.loadUsers()
    },
    async editUser (id) {
      this.currentUser = id
      const { data } = await Rest.get(`/users/${id}`, authHeaders())
      this.user.name = data.name
      this.user.position = data.position
      this.user.cabinet = data.cabinet
      this.user.department = data.department
      this.user.pcName = data.pcName
    },
    async saveUser () {
      await Rest.put(`/users/${this.currentUser}`, {
        name: this.user.name ?? '',
        position: this.user.position ?? '',
        cabinet: this.user.cabinet ?? '',
        department: this.user.department ?? '',
        pcName: this.user.pcName ?? ''
      }, authHeaders())
      this.clearForm()
      await this.loadUsers()
    },
    clearForm () {
      this.currentUser = ''
      this.user.name = ''
      this.user.position = ''
      this.user.cabinet = ''
      this.user.department = ''
      this.user.pcName = ''
    }
  },
  mounted () {
    this.loadUsers()
  }
}
</script>

<style scoped lang="stylus">
  table
    width: 100%
</style>
