<template>
  <form
    action=""
    class="form-control mb-5 mt-5"
    @submit.prevent="registration"
  >
    <h3 class="form-title">Registration</h3>
    <div class="mb-3">
      <label for="username" class="form-label" >Username</label>
      <input
        id="username"
        type="text"
        class="form-control form"
        v-model="reg.user"
      >
    </div>
    <div class="mb-3">
      <label for="password" class="form-label">Password</label>
      <input
        id="password"
        type="password"
        class="form-control"
        v-model="reg.password"
      >
    </div>

    <button
      type="submit"
      class="btn btn-dark">Submit</button>
  </form>
</template>

<script>
import Rest, { authHeaders } from '../rest'
export default {
  data () {
    return {
      reg: {
        user: '',
        password: ''
      }
    }
  },
  methods: {
    async registration () {
      try {
        const { data } = await Rest.post('/registration', this.reg, authHeaders())
        console.log(data)
      } catch (e) {
        console.error(e.message)
      }
    }
  }
}

</script>

