<template>
  <form
    v-if="getFormStatus"
    action="" class="d-flex"
  >
    <label class="form-label flex-wrap">
      name
      <input :disabled="isEdit" class="form-control" type="text" v-model="computerData.name">
    </label>
    <label class="form-label">
      ram
      <input class="form-control" type="text" v-model="computerData.ram">
    </label>
    <label class="form-label">
      processor
      <input class="form-control" type="text" v-model="computerData.processor">
    </label>
    <label class="form-label">
      ipAddress
      <input class="form-control" type="text" v-model="computerData.ipAddress">
    </label>
    <label class="form-label">
      cabinet
      <input class="form-control" type="text" v-model="computerData.cabinet">
    </label>
    <label class="form-label">
      login
      <input class="form-control" type="text" v-model="computerData.login">
    </label>
    <label class="form-label">
      password
      <input class="form-control" type="text" v-model="computerData.password">
    </label>

    <div class="d-flex">
      <button
        @click.prevent="addComputer"
        class="btn btn-outline-dark"
        v-if="!isEdit"
      >Add</button>
      <button
        @click.prevent="saveComputer"
        class="btn btn-outline-success"
        v-if="isEdit"
      >Save</button>
      <button
        @click.prevent="clearForm"
        class="btn btn-outline-secondary"
      >Cancel/Clear</button>
    </div>

  </form>
  <button
    class="btn btn-dark"
    @click="toggleForm"
  >{{getFormStatus ? 'Hide' : 'Show'}} form</button>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters(['computerData', 'isEdit', 'getFormStatus'])
  },
  methods: {
    async addComputer () {
      this.$store.dispatch('addComputer', {
        url: 'http://10.70.62.240:8080/computers'

      })
    },
    async saveComputer () {
      this.$store.dispatch('saveComputer', {
        url: 'http://10.70.62.240:8080/computers'
      })
    },
    clearForm () {
      this.$store.dispatch('clearForm')
    },
    toggleForm () {
      this.$store.commit('toggleForm')
    }
  }

}
</script>

<style scoped>

</style>
