<template>
  <div class="header">
    <div class="container">
      <div class="d-flex justify-content-between align-items-center">
        <div class="logo">Clinic</div>
        <ul class="nav">
          <li class="nav-item">
            <router-link to="/cabinets">Cabinets</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/computers">Computers</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/users">Users</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/printers">Printers</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/login">Login</router-link>
          </li>
          <li class="nav-item">
            <a href="" @click.prevent="logout">Logout</a>
          </li>
          <li class="nav-item">
            <router-link to="/registration">Registration</router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>

</template>

<script>
  import {useRouter} from 'vue-router'
  import {useStore} from 'vuex'

  export default {
    setup() {
      const router = useRouter()
      const store = useStore()

      return {
        logout: () => {
          store.commit('auth/logout')
          router.push('/auth')
        }
      }
    }
  }

</script>

<style scoped lang="stylus">
  .header
    padding: 16px 0
    background: #3B429F

  .logo
    font-weight: bold;
    font-size: 32px
    color: #fff

  .nav-item
    &:not(:last-child)
      margin-right: 12px

    a
      color: #fff
      text-decoration none

      &:hover
        text-decoration underline
</style>
