import { createStore, createLogger } from 'vuex'
import auth from '../modules/auth.module'
import Rest, { authHeaders } from '../rest'

const plugins = []

if (process.env.NODE_ENV === 'development') {
  plugins.push(createLogger())
}

export default createStore({
  plugins,
  state () {
    return {
      message: null,
      // =================
      isEdit: false,
      currentComputer: null,
      computerData: {
        name: '',
        ram: '',
        processor: '',
        ipAddress: '',
        cabinet: '',
        login: '',
        password: ''
      },
      computers: [],
      formIsShowed: false
    }
  },
  mutations: {
    setMessage(state, message) {
      state.message = message
    },
    clearMessage(state){
      state.message = null
    },
    // ===============
    toggleEdit (state, payload) {
      state.isEdit = payload.value
    },
    toggleCurrentComputer (state, payload) {
      state.currentComputer = payload.value
    },
    getComputerData (state, payload) {
      state.computerData = {
        name: payload.name,
        ram: payload.ram,
        processor: payload.processor,
        ipAddress: payload.ipAddress,
        cabinet: payload.cabinet,
        login: payload.login,
        password: payload.password
      }
    },
    clearForm (state) {
      state.isEdit = false
      state.currentComputer = null
      state.computerData = {
        name: '',
        ram: '',
        processor: '',
        ipAddress: '',
        cabinet: '',
        login: '',
        password: ''
      }
    },
    loadComputers (state, payload) {
      state.computers = Object.keys(payload).map(key => {
        return {
          ...payload[key]
        }
      })
    },
    toggleForm (state, payload) {
      if (payload) {
        state.formIsShowed = payload
      } else {
        state.formIsShowed = !state.formIsShowed
      }
    }
  },
  actions: {
    setMessage({commit}, message) {
      commit('setMessage', message)
      setTimeout(() => {
        commit('clearMessage')
      }, 6000)
    },
    // ====================
    async toggleEdit ({ commit }, payload) {
      await commit('toggleEdit', payload)
    },
    toggleCurrentComputer ({ commit }, payload) {
      commit('toggleCurrentComputer', payload)
    },
    async getComputerData ({ commit, getters }) {
      const { data } = await Rest.get(`http://10.70.62.240:8080/computers/${getters.currentComputer}`, authHeaders())
      await commit('getComputerData', data)
    },
    async clearForm ({ commit }) {
      commit('clearForm')
      commit('toggleForm', false)
    },
    async loadComputers ({ commit }, payload) {
      try {
        const { data } = await Rest.get(payload, authHeaders())
        commit('loadComputers', data)
      } catch (e) {
        console.error(e.message)
      }
    },
    async addComputer ({ commit, getters, dispatch }, payload) {
      try {
        await Rest.post(payload.url, getters.computerData, authHeaders())
        dispatch('loadComputers', 'http://10.70.62.240:8080/computers')
      } catch (e) {
        console.error(e.message)
      }
    },
    async editComputer ({ dispatch, commit }) {
      try {
        await dispatch('getComputerData')
        commit('toggleForm', true)
      } catch (e) {
        console.error(e.message)
      }
    },
    async saveComputer ({ commit, dispatch, getters }, payload) {
      try {
        await Rest.put(`${payload.url}/${getters.currentComputer}`, getters.computerData, authHeaders())
        dispatch('loadComputers', 'http://10.70.62.240:8080/computers')
        commit('clearForm')
        commit('toggleForm', false)
      } catch (e) {
        console.error(e.message)
      }
    },
    async deleteComputer ({ commit, dispatch }, payload) {
      try {
        await Rest.delete(`${payload.url}/del?name=${payload.id}`, authHeaders())
        dispatch('loadComputers', 'http://10.70.62.240:8080/computers')
        commit('clearForm')
        commit('toggleForm', false)
      } catch (e) {
        console.error(e.message)
      }
    }
  },
  getters: {
    isEdit (state) {
      return state.isEdit
    },
    currentComputer (state) {
      return state.currentComputer
    },
    computerData (state) {
      return state.computerData
    },
    getComputers (state) {
      return state.computers
    },
    getFormStatus (state) {
      return state.formIsShowed
    }
  },
  modules: {
    auth
  }
})
